
































































































import { Vue, Component } from "vue-property-decorator";
import { State, Mutation } from "vuex-class";
import { User } from "@/store/modules/user/types";
@Component
export default class PasswordUpdate extends Vue {
  @State("user", { namespace: "User" }) user!: User;
  @Mutation("User/UserLogout") userLogout!: Function;
  private oldUserPassword: string = "";
  private againNewUserPassword: string = "";
  private newUserPassword: string = "";
  private valid: Boolean = true;
  private overlay: Boolean = false;

  clear() {
    this.oldUserPassword = "";
    this.newUserPassword = "";
    this.againNewUserPassword = "";
  }
  update() {
    if ((this.$refs.form as Vue & { validate: () => boolean }).validate()) {
      this.overlay = true;
      this.axios
        .post(
          "/user/change_password",
          JSON.stringify({
            old_password: this.oldUserPassword,
            password: this.newUserPassword
          })
        )
        .then(data => data.data)
        .then(({ ok }) => {
          this.serverLogout();
        })
        .catch(data => {
          this.overlay = false;
          this.$toasted.show(`更新失敗`, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
  serverLogout() {
    this.axios
      .get("/logout")
      .then(data => data.data)
      .then(({ ok }) => {
        this.$toasted.show(`請重新登入`, {
          type: "success",
          position: "top-right",
          duration: 3000
        });
        this.userLogout();
        this.overlay = false;
        this.$router.push("/login");
      })
      .catch(data => {});
  }
}
